:root {
  --spacing: 8px;
  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  --animation-duration: 250ms;
}

img {
  display: block;
  max-width: 100%;
}

body {
  font-family: Helvetica, Sans-Serif;
  color: #444444;
  font-size: 9pt;
  background-color: #FAFAFA;
  background-color: #eeeeee;
  line-height: 1.5;
  padding: 60px 15px 0 15px;
  
  } 

  a {
    text-decoration: none;
    color: #444444;
  }

